body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b4c7ff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-name {
  color: white;
  text-align: center;
  font-size: 4rem;
}

.projects {
display: flex;
flex-direction: row;
flex-wrap: wrap;
align-items: center;
justify-content: center;
}

.project {
  transition: all 0.5s ease-in-out;
  position: relative;
  top: 0px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
  background-color: rgb(16, 115, 213);
  padding: 10px;
  border-radius: 25px;
  color: white;
  margin: 10px;
  text-align: center;
}

.project:hover {
  top: -10px;
}

.projectTitle {
  font-size: 2rem;
}

.projectDescription {
  font-size: 1.2rem;
}

a {
  text-decoration: none;
  color: white;
}

.skills {
  margin-top: 40px;
  background-color: rgb(16, 115, 213);
  margin-bottom: 45px;
}

.skillsTitle {
  font-size: 2rem;
  text-align: center;
  color: white;
}

.project2 {
  transition: all 0.5s ease-in-out;
  position: relative;
  top: 0px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
  background-color: rgb(49, 137, 226);
  padding: 10px;
  border-radius: 25px;
  color: white;
  margin: 10px;
  text-align: center;
}

.project2:hover {
  top: -10px;
}

.skillse {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.projectDescription2 {
  font-size: 1.5rem;
}

.skillsTitle2 {
  font-size: 3rem;
  text-align: center;
  color: white;
}